@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
.Home{
   
    width:98.5vw;
    padding-top: 50px;
    
    height: 105vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

 font-size: 55px;
 font-family: 'Great Vibes', 'cursive';
 transition: 0.5s;

 
}
.Signup1{
    width: 200px;
    padding: 15px 70px;
    text-align: center;
    margin-top: 800px;
    margin-right: 180px;
    border-radius: 25px;
    font-weight: bold;
    border: 2px solid #0056d5
    ;
    background: transparent;
    color: black;
    cursor: pointer;
    position: relative;
    
    text-align: center;
    text-transform: uppercase;
   
    
    font-family:'Roboto', sans-serif;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
    transition: 0.5s;


}
.Login{
    
    width: 200px;
    padding: 15px 70px;
    text-align: center;
    margin: 20px 21px;
    border-radius: 25px;
    font-weight: bold;
    border: 2px solid #0056d5;
    background: transparent;
    color: black;
    cursor: pointer;
    position: relative;
    box-sizing: border-box;
    
    text-align: center;
    text-transform: uppercase;
    left: 190px;
    bottom: 78px;
    font-family:'Roboto', sans-serif;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
    transition: 0.5s;

}
span{
    background:#0056d5


    ;
    height: 100%;
    width: 0%;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 25px;
    z-index: -1;
    transition: 0.5s;
}
.Signup1:hover span{
    width: 100%;
}
p{
  
    font-size: 40px;
    margin-left: 30%;
    margin-top: 15px;
}
.Login:hover span{
    width: 100%;


}

.banner{
    border: 6px double black;
    border-radius:25px ;
    position: absolute;
    margin-left: 45px;
    height: 550px;
    width: 800px;
    

    
}
.Signup1:hover{
    color: white;
    
}
.Login:hover{
    color: white;
    
}