}
#Submit{
    width: 200px;
    padding: 15px 0px;
    text-align: center;
    background: transparent;
    border-radius: 25px;
    font-weight: bold;
    border:2px solid #0056d5;
    background-color: #0056d5;
    color: whitesmoke;
   
   
    font-size: 17px;
    margin-top: 20px;
    margin-right: 10px;
    cursor: pointer;
}
#Login-Success{
    font-size: 26px;
   position: relative;
   top: 27px;
 left: 150px;
    font-weight: 400;
    color: green;
    display: none;
   
}
#href-login{
    color: #0056d5;
    font-size: 15.75px;
    font-style: inherit;
    font-weight: 500;
    margin-top: 6px;
}

#LabelSignupAs{
    font-weight: 800;
    font-style: italic;
    font-size: 18px;
   
    

}
#NativeSelect{
    width: 300px;
    height: 30px;
    background: none;
    color: black;
    font-style: italic;
    font-weight: bolder;
    
}
.div-select{
    margin-top: 23px;
    margin-bottom: 23px;
}
#Signup-Submit{
    width: 120px;
    
    text-align: center;
    font-weight: bold;

   
   
    font-size: 15px;
    margin-top: 22px;
}
#Signup-Submit:hover{
    background-color: #0056d5;
    color: whitesmoke;
    border-color: #0056d5;
}
#NoteForSubscription{
    font-size: 13px;
    color: red;
    font-weight: 500;
    margin-top: 30px;
}
#SecondNoteForSubscription{
    font-size: 13px;
    color: red;
    font-weight: 500;
    margin-top: 10px;

}