@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');


.Contact{
    width: 99vw;
    margin-top: 20px;
}
.Contact h1{
    width: fit-content;
    transform: translateX(500px);
    font-size: 80px;
    font-weight: 600;
    font-family: 'Great Vibes', 'cursive';
    margin-top:110px ;
}
#contact-name{
    transform: translateX(0%);
    margin-top: 50px;
    width: 370px;
    background: transparent;
    height: 48px;
    border: 2px solid black;
    border-radius: 25px;
    color: black;
    padding-left: 24px;
    font-size: 17px;
    outline: none;
    margin-bottom: 25px;
}
#contact-email{
    transform: translateX(0%);
    margin-top: 23px;
    width: 370px;
    background: transparent;
    height: 48px;
    border: 2px solid black;
    border-radius: 25px;
    color: black;
    padding-left: 24px;
    font-size: 17px;
    outline: none;
    margin-bottom: 25px;
}
#Contact-form{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction:column ;
    box-sizing: border-box;

}
#contact-textarea{
    font-style: inherit;
    font-family:'Times New Roman', Times, serif;
    font-size: 15px;
    outline: none;
   
    border: 2px solid black;
    width: 450px;
    height: 300px;
    border-radius: 25px;
    padding-top: 10px;
    padding-left: 23px;

}
#Contact-Submit{
    width: 200px;
    padding: 15px 0px;
    text-align: center;
    background: transparent;
    border-radius: 25px;
    font-weight: bold;
    border:2px solid #0056d5;
    background-color: #0056d5;
    color: whitesmoke;
   
   
    font-size: 17px;
    margin-top: 20px;
    margin-right: 10px;
    cursor: pointer;
}