*{
    padding: 0;
    margin: 0;
}

.Navbar{
    
    width: 99.5%;
   
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    
    transition: 0.5s;
    left: 0;
    
    
}
.logo{
    display: inline;
    

}
.MainApp-logo{

    height: 69px;
    width: 202.5px;
 
    margin-top: 3px;
}
.ul-nav{
display: flex;
align-items: center;
margin-right:50px ;
justify-content: center;


}
.ul-nav li{
    
    margin-left:19px ;
    margin-right: 19px;
    list-style: none;
    font-style: none;
    outline: none;
    text-decoration: none;
    padding:5px ;
    position: relative;
    font-weight:450;
    margin-top: 20px;

}
.ul-nav li a{

    font-size: 16px;
    padding-left: 8px;
    padding-right: 8px;
    text-decoration: none;
    color: black;
}
.ul-nav li a::after{
    content: "";
    height: 3px;
    width: 0%;
    background:  #0056d5;
    left: 0;
    bottom: -5px;
    position: absolute;
    transition: 0.5s;

}

.ul-nav li a:hover::after{

    width: 99%;
    
}
.ul-nav li a:hover{
    color: #0056d5;
}
.Navbar::after{
    content: "";
    height: 3px;
    width: 100%;
    background: black;
    left: 0;
    bottom: -6px;
    position: absolute;
    transition: 0.5s;
}
.switch{
    
    position: relative;
top: 8px;
height: 65px;
width: 120px;
opacity: 0.5;
border-radius: 25px;
padding-top: 5px;
padding-left: 15px;


}   



#label-mode{
    display: inline;
    position: relative;
    bottom: 5px;
    right: 20px;
    font-size: 13px;
    color: black;
    font-weight: 500;
    font-style: italic;


}